.tracks {
    margin-left: 20%;
    align-self: center;
    text-align: left;
    font-size: 0.5em;
}

.track__name {
    color: #F00;
}

.track__artist {
    color: #0F0;
}
.track__artist::before {
    color: white;
    content: " - by: "
}

.track__album {
    color: #00F;
}
.track__album::before {
    color: white;
    content: ", album: "
}

.search-result:hover {
    background-color: grey;
}

.search-result:active {
    background-color: #0F0;
}